
































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import Header from '@/components/layout/Header.vue';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import { showToast } from '@/utils/global-functions';
import AppearTransition from '@/components/utilities/transitions/AppearTransition.vue';
import { User } from '@/modules/auth/interfaces/user.interface';
import { auth, verifLevels } from '@/store/namespaces';
import AuthMethods from '@/store/auth/methods/auth.methods';
import VerifLevelMethods from '@/store/verificationLevels/methods/verificationLevels.methods';
import {levelNames} from '@/modules/verificationLevels/constants/verifLevels.constants'
import {getStatusColor, getStatusIcon, getStatusName} from '@/modules/verificationLevels/functions/verifLevels.functions'
import { Limitation, LimitationType } from '../interfaces/limitations.interface';
import Loader from '@/components/utilities/Loader.vue';
import { Country } from '@/modules/countries/interfaces/countries.interface';
import { VerifLevelReq } from '../interfaces/requirement.interface';
import { LEVEL_LIMITATION_STATUS } from '../emptyStates/verifLevels.emptyState';

@Component({
    components: {
        Header,
        SvgIcon,
        AppearTransition,
        Loader
    },
})
export default class VerificationLevelsFeatures extends Vue {
    $refs : any ={};
    levels =  LEVEL_LIMITATION_STATUS;
    loading = true;
    tableLimitations = []


    async mounted(){
      try{
       this.$refs.loader.showLoader();
        //this.resetCurrentFeatures();
        await this.verifyCurrentLevel()
        let countryId = this.user.id_resid_country;
        if (typeof countryId === 'number') countryId = countryId.toString()
        await this.fetchLimitations(countryId);
        this.limitations.name_limitations.forEach(el =>{
            let limitations : any = {};
            limitations.id_limitation =el.id_limitation;
            limitations.name = el.name
            limitations.limitations = []
            this.limitations.limitations.forEach(e=>{
                if (e.id_limitation === el.id_limitation){
                    limitations.limitations.push(e)
                }
            })
            this.tableLimitations.push(limitations)
        })
        this.tableLimitations[0].limitations.push({def: 'Para Plan Jurídico contactanos via empresas@criptoremesa.com',destiny_countries:[]})
        this.tableLimitations[1].limitations.push({def: 'Múltiples beneficiarios',destiny_countries:[]})
        this.tableLimitations[2].limitations.push({def: 'Para Plan Jurídico contactanos via empresas@criptoremesa.com',destiny_countries:[]})
        this.tableLimitations[3].limitations.push({def: 'Para Plan Jurídico contactanos via empresas@criptoremesa.com',destiny_countries:[]})
        this.tableLimitations[4].limitations.push({def: 'Para Plan Jurídico contactanos via empresas@criptoremesa.com',destiny_countries:[]})
        this.tableLimitations[5].limitations.push({def: 'Para Plan Jurídico contactanos via empresas@criptoremesa.com',destiny_countries:[]})
        this.tableLimitations[6].limitations.push({destiny_countries: [
                {
                    "id_country": 1,
                    "name_country": "Venezuela",
                    "viewing_name": "Venezuela",
                    "country_iso_code": "VE"
                },
                {
                    "id_country": 2,
                    "name_country": "Colombia",
                    "viewing_name": "Colombia",
                    "country_iso_code": "CO"
                },
                {
                    "id_country": 3,
                    "name_country": "Peru",
                    "viewing_name": "Perú",
                    "country_iso_code": "PE"
                },
                {
                    "id_country": 6,
                    "name_country": "Brazil",
                    "viewing_name": "Brasil",
                    "country_iso_code": "BR"
                },
                {
                    "id_country": 4,
                    "name_country": "Chile",
                    "viewing_name": "Chile",
                    "country_iso_code": "CL"
                },
                {
                    "id_country": 5,
                    "name_country": "Argentina",
                    "viewing_name": "Argentina",
                    "country_iso_code": "AR"
                },
                {
                    "id_country": 7,
                    "name_country": "Dominican Republic",
                    "viewing_name": "Rep. Dom.",
                    "country_iso_code": "DO"
                },
                {
                    "id_country": 9,
                    "name_country": "Spain",
                    "viewing_name": "España",
                    "country_iso_code": "ES"
                }
            ]})
        this.tableLimitations[7].limitations.push({def: 'Compra de Criptomonedas. Venta de criptomonedas. Pago nómina. Pago servicios',destiny_countries:[]})
        this.$refs.loader.hideLoader();
      }catch(e){
          showToast('Error al cargar la página', 'error');
          this.$router.push({name: 'Dashboard'})
      }
    }

    getText(level: number, idLimitation: number){
        return this.limitations.limitations.find(el => {
                if(el.id_limitation === idLimitation && el.id_verification === level) 
                    return el
                }
        )
    }

    @Watch('user.verif_level_apb')
    async watchVerifications(){
        await this.verifyCurrentLevel()
    }

    resetLevelsStatus(){
        this.levels.forEach(el=>{
            el.currentLevel = false;
            el.approved = undefined;
            el.badge.color = ''
            el.badge.icon = ''
            el.badge.name = ''
        })
    }

    async verifyCurrentLevel(){
        await this.fetchVerifLevelReqs(this.user.email_user);
        this.resetLevelsStatus();
        if (this.verifLevelReqs.level_one === null && this.verifLevelReqs.level_two === null){
            this.levels[0].badge.color = getStatusColor(this.user.verif_level_apb);
            this.levels[0].badge.icon = getStatusIcon(this.user.verif_level_apb);
            this.levels[0].badge.name = getStatusName(this.user.verif_level_apb); 
            this.levels[0].approved = this.user.verif_level_apb;
            if (this.user.verif_level_apb === true) this.levels[1].currentLevel = true;
            else this.levels[0].currentLevel = true;
        }
        if (this.verifLevelReqs.level_one !== null){
            let approved = true;
            for (let i=0;i<this.verifLevelReqs.level_one.length;i++){
                if (this.verifLevelReqs.level_one[i].req_confirm_action === null){
                    this.setBadges(1);
                    approved = null;
                    break;
                }
                if (this.verifLevelReqs.level_one[i].req_confirm_action === false)
                    approved = false
            }
            this.levels[0].badge.color = getStatusColor(true);
            this.levels[0].badge.icon = getStatusIcon(true);
            this.levels[0].badge.name = getStatusName(true); 
            this.levels[0].approved = true;
            this.levels[1].approved = approved;
            this.levels[1].badge.color = getStatusColor(approved);
            this.levels[1].badge.icon = getStatusIcon(approved);
            this.levels[1].badge.name = getStatusName(approved); 
            if (approved !== true) this.levels[1].currentLevel = true
            else this.levels[2].currentLevel = true;
        }else this.levels[1].approved = undefined;
        if (this.verifLevelReqs.level_two !== null){
            let approved = true;
            for (let i=0;i<this.verifLevelReqs.level_two.length;i++){
                if (this.verifLevelReqs.level_two[i].req_confirm_action === null){
                    this.setBadges(2);
                    approved = null;
                    break;
                }
                if (this.verifLevelReqs.level_two[i].req_confirm_action === false)
                    approved = false
            }
            this.levels[0].approved = true;
            this.levels[1].approved = true;
            this.levels[2].approved = approved;
            this.levels[0].badge.color = getStatusColor(true);
            this.levels[0].badge.icon = getStatusIcon(true);
            this.levels[0].badge.name = getStatusName(true); 
            this.levels[1].badge.color = getStatusColor(true);
            this.levels[1].badge.icon = getStatusIcon(true);
            this.levels[1].badge.name = getStatusName(true); 
            this.levels[2].badge.color = getStatusColor(approved);
            this.levels[2].badge.icon = getStatusIcon(approved);
            this.levels[2].badge.name = getStatusName(approved); 
            this.levels[2].currentLevel = true;
        }else this.levels[2].approved = undefined;

    }

    setBadges(verifLevel: number | string){
        Array.from(Array(verifLevel).keys()).forEach(el =>{
            this.levels[el].badge.color = getStatusColor(true);
            this.levels[el].badge.name = getStatusName(true);
            this.levels[el].badge.icon = getStatusIcon(true);
          })
    }

    getCountriesList(countriesList: Country[]){
        let countriesString = ''
        countriesList.forEach(el=>{
            if (countriesString.length > 0) countriesString+=', '
            countriesString += el.viewing_name
        })
        return countriesString
    }


    isActiveTableItem(level: number, limitation: Limitation): string{
        let classes  = ''
        if (level !== 3 && this.levels[level].currentLevel === true) classes+= 'active'
        if (limitation.is_allowed) classes+= ' fs-smaller'
        if ((level + 1 )=== this.tableLimitations[0].limitations.length) classes+= ' extra'
        return classes
    }


  @auth.Getter(AuthMethods.getters.GET_USER_DATA)
    user!: User;
  @verifLevels.Action(VerifLevelMethods.actions.FETCH_LIMITATION_BY_COUNTRY)
  fetchLimitations!: (countryId: string) => Promise<boolean>
  @verifLevels.Getter(VerifLevelMethods.getters.GET_LIMITATIONS_BY_COUNTRY)
  limitations!: {limitations: Limitation[], name_limitations: LimitationType[]};
  @verifLevels.Action(VerifLevelMethods.actions.FETCH_VERIF_LEVEL_REQUIREMENTS)
  fetchVerifLevelReqs!: (emailUser: string) => Promise<boolean>
  @verifLevels.Getter(VerifLevelMethods.getters.GET_VERIF_LEVEL_REQUIREMENTS)
  verifLevelReqs!: {level_one: VerifLevelReq[], level_two: VerifLevelReq[], email_user: string};
}
